<template>
    <div class="input-fields">
        <div class="field">
            <label for="balance">Balance</label>
            <input type="number" id="balance" placeholder="USDT" v-model="balance">
        </div>
        <div class="field">
            <label for="interest">Interest Rate</label>
            <input ref="interest" type="number" id="interest" placeholder="%" v-model="interest">
        </div>
        <div class="field">
            <label for="visa">Visa</label>
            <select ref="visa" id="visa" v-model="visa">
                <option value="0" disabled selected hidden>Select Visa</option>
                <option value="4">M1: $4/day</option>
                <option value="14">M2: $14/day</option>
                <option value="39">M3: $39/day</option>
                <option value="64">M4: $64/day</option>
                <option value="112">M5: $112/day</option>
                <option value="132">M6: $132/day</option>
                <option value="151.8">M7: $151.8/day</option>
                <option value="171.6">M8: $171.6/day</option>
            </select>
        </div>
        <div class="field">
            <button @click="generate">Calculate</button>
        </div>
    </div>
    <div v-if="result" class="results">
        <table>
            <tr>
                <th>Day</th>
                <th>Prev balance</th>
                <th>Interest</th>
                <th>Cumulative Interest</th>
                <th>Balance</th>
                <th>Flat Earning</th>
                <th>Withdrawal Fee</th>
                <th>Net</th>
            </tr>
            <tr v-for="row, key in result" :key="key" :class="{'odd-index': row.day % 2}">
                <td>{{ row.day }}</td>
                <td>{{ row.prev_balance }}</td>
                <td>{{ row.current_interest }}</td>
                <td>{{ row.cumulative_interest }}</td>
                <td>{{ row.current_balance }}</td>
                <td :class="{'net-income': row.net > row.flat_earning, 'net-loss': row.net < row.flat_earning}">{{ row.flat_earning }}</td>
                <td>{{ row.withdrawal_fee }}</td>
                <td :class="{'net-income': row.net > row.flat_earning, 'net-loss': row.net < row.flat_earning}">{{ row.net }}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    name: 'App',
    components: {
    },
    data() {
        return {
            balance: null,
            interest: null,
            visa: 0,
            result: null
        };
    },
    methods: {
        calculate: function (start, end, balance, interest, visa) {
            let res = [];
            let prev_balance = balance;
            let current_interest = 0;
            let cumulative_interest = 0;
            let flat_earning = 0;
            for (let i = start; i <= end; i++) {
                current_interest = (prev_balance * (1 + (interest / 100))) - prev_balance;
                cumulative_interest += current_interest;
                flat_earning = balance + (visa * i);
                res.push({
                    day: i,
                    prev_balance: parseFloat(prev_balance.toFixed(5)),
                    current_interest: parseFloat(current_interest.toFixed(5)),
                    cumulative_interest: parseFloat(cumulative_interest.toFixed(5)),
                    current_balance: parseFloat((flat_earning + cumulative_interest).toFixed(5)),
                    flat_earning: parseFloat(flat_earning.toFixed(5)),
                    withdrawal_fee: parseFloat(((flat_earning + cumulative_interest)/10).toFixed(5)),
                    net: parseFloat(((flat_earning + cumulative_interest)*(9/10)).toFixed(5))
                });
                prev_balance = flat_earning + cumulative_interest;
            }
            return res;
        },
        generate: function () {
            if (this.$refs.visa.value == 0 || !this.$refs.interest.value) {
                return;
            }
            this.result = this.calculate(1, 30, this.balance || 0, this.interest || 0, this.visa || 0);
        }
    }
}
</script>

