    display: flex;
    flex-direction: column;
    align-items: center;
    margin-block: 1rem;
}

.field>input {
    text-align: center;
}

.net-income {
    background-color: rgb(0, 64, 0);
}

.net-loss {
    background-color: rgb(128, 0, 0);
}

.odd-index {
    background-color: rgb(56, 56, 56);
}

.results {
    margin-block: 5rem;
}

table {
    margin: 0 auto;
}

table,
th,
td {
    border: 1px solid white;
}
</style>
